#buttonsContact ul{
    list-style: none;
    color: white;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
#buttonsContact ul li{
    border-radius: 20px;
    padding: 1rem;
    cursor: pointer;
    transition: 300ms;
    width: fit-content;
    height: fit-content;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}
#buttonsContact ul li:hover{
background-color: aliceblue;
}
#buttonsContact ul li:hover a{
    color: black;
    }

#buttonsContact ul li a{
    color: white;
}